body {
  margin: 0;
  padding: 0;
  padding-bottom: 20px;
  font-family: 'Roboto', sans-serif !important;
  font-size: 16px !important;
}

.background-container, .background-image {
    width: 100%;
    height: 35%;
    position: absolute;
    top: 0;
    left: 0;
}

.background-image {
    height: 100%;
}

